import BaseModule from './../Base.module';
import ModalController from './controller/Modal.controller';

/**
 * @extends {BaseModule}
 */
class ModalModule extends BaseModule {

	constructor ( config ) {

		super( config );
	}

	init () {
		super.init();

		const controllerInstance = new ModalController( this.config );

		this.modal = controllerInstance.init( this.node );
	}
}

export default ModalModule;



